
import {Options, Vue} from 'vue-class-component';
import {Expense} from "../model/expense"
import {format as money_format} from 'v-money3';
import {Params} from "../model/param"

@Options({
    props: {
      visible: Boolean,
      view_period: Number,
      expense: Expense,
      params: Params,
      operation: String,
      depth: Number,
  }
})

export default class OutputTable extends Vue {
    child_visible = 0;
    alias_money_format = money_format;
    format(v: number) : string {
      return "$" + money_format(v);
    }
    periodic_amount(expense: Expense, params: Params, view_period: number) : number {
       const amortized_upfront = (expense.upfront_amount / (params.config.term*Expense.ONE_YEAR)) * view_period ;
       const repeated_amount = (expense.repeat_period == 0) ? 0 : ((expense.repeating_amount / expense.repeat_period ) * view_period);
       return amortized_upfront + repeated_amount;
    }
}
