
import {Options, Vue } from 'vue-class-component';
import {ConfigLoad} from "./model/config"
import {Params} from "./model/param"
import {CostOfOwnership} from "./model/cost_of_ownership"
import {LoanAmount} from "./model/loan_amount"


@Options({
  props: {
    query: Object
  },
})

export default class App extends Vue {
     params!: Params;
     data(): Record<string, unknown>  {
         var params = new Params();
         const postcode: string|undefined = this.$route.query["postcode"] as string || undefined;
         ConfigLoad(params,postcode);
         return {
             'params' : params,
         }
     }
    loan_amount(): LoanAmount {
        return new LoanAmount(this.params);
    }
    cost_of_ownership(): CostOfOwnership {
        const loan_amount = new LoanAmount(this.params);
        const cost_of_ownership = new CostOfOwnership(this.params, 
                                                      loan_amount.upfront_amount);
        return cost_of_ownership;
    }
}
